<template>
    <div>
        <Button @click="submit">Submit</Button>
        <Schedule v-model="schedule" />
        <pre>{{ schedule }}</pre>
    </div>
</template>

<script>
export default {
    data: () => ({ schedule: {} }),

    methods: {
        async submit() {
            const res = await this.$http.post('/test', {
                schedule: this.schedule,
            })
            console.log(res.data)
        },
    },
}
</script>
